:root {
  --flatify__blockquote-accent-color: var(--flatify__color-accent-primary);
}

blockquote {
  position: relative;
  z-index: 0;
  overflow: hidden;
  line-height: var(--flatify__large-line-height);
  margin: 0 0 var(--flatify__paragraph-mb);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
  border-style: solid;
  border-color: var(--flatify__blockquote-accent-color);
  border-width: 0 0 0 0.5em;
}
blockquote:before {
  content: " ";
  position: absolute;
  z-index: -1;
  opacity: 0.1;
  background-color: currentColor;
  mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  -webkit-mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  filter: brightness(100%);
}
blockquote:not(.large) {
  font-size: 1em;
  line-height: var(--flatify__large-line-height);
  padding: 0.75em 1.25em;
  border-radius: calc(var(--flatify__border-radius) * 0.5) var(--flatify__border-radius) var(--flatify__border-radius) calc(var(--flatify__border-radius) * 0.5);
}
blockquote:not(.large):before {
  top: 1em;
  width: 10em;
  height: 10em;
  left: 1em;
}
blockquote.large {
  font-size: 1.25em;
  border-width: 0.5em 0;
  padding: 1.25em 0.75em;
  margin-top: 2em;
}
blockquote.large:before {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 15em;
  height: 15em;
  margin: auto;
}
blockquote.large:not(.text-align-fill) {
  text-align: center;
}
blockquote cite {
  display: block;
  font-size: 0.64em;
  font-style: normal;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  padding-top: 1.5em;
  opacity: 0.8;
}

figure {
  position: relative;
  width: max-content;
  max-width: 100%;
  height: max-content;
  margin: 0 0 1em;
  border-radius: var(--flatify__border-radius);
}
figure img {
  width: 100%;
  height: 100%;
  border-radius: var(--flatify__border-radius);
}
figure figcaption {
  font-size: 0.9em;
  padding: 1em 0;
