  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-left: 1em;
  padding-right: 1em;
  color: var(--flatify__txt-color-inverted);
  background: linear-gradient(0, var(--flatify__txt-color-dark), transparent);
}

hr {
  margin: 1em 0;
  opacity: 0.5;
  color: currentColor;
  background-color: currentColor;
  border: none;
